import React, { Component } from 'react'
import { List, Picker } from 'antd-mobile'

const getYear = () => {
  let arr = []
  for (let i = 2000; i < 2050; i++) {
    arr.push({label: i, value: i})
  }
  return arr
}
const getMonth = () => {
  let arr = []
  for (let i = 1; i <= 12; i++) {
    arr.push({label: `${i}月`, value: i})
  }
  return arr
}
const getWeek = () => {
  let arr = []
  for (let i = 1; i < 53; i++) {
    arr.push({label: `${i}周`, value: i})
  }
  return arr
}
const dataW = [
  getYear(),
  getWeek(),
]
const dataM = [
  getYear(),
  getMonth(),
]


class DateMonthWeek extends Component {

  constructor (props) {
    super(props)
    this.state = {}
  }

  render () {
    return (
      <div>
        <Picker
          data={this.props.type === 'm' ? dataM : dataW}
          title="开始日期"
          cascade={false}
          extra="开始日期"
          value={this.props.beginDate}
          onChange={value => this.props.onTimeChange(value, 'beginDate')}
          onOk={value => this.props.onTimeChange(value, 'beginDate')}
          format={(labels) => labels.join('-')}
        >
          <List.Item arrow="horizontal">开始日期</List.Item>
        </Picker>
        <Picker
          data={this.props.type === 'm' ? dataM : dataW}
          title="结束日期"
          cascade={false}
          extra="结束日期"
          value={this.props.endDate}
          onChange={value => this.props.onTimeChange(value, 'endDate')}
          onOk={value => this.props.onTimeChange(value, 'endDate')}
          format={(labels) => labels.join('-')}
        >
          <List.Item arrow="horizontal">结束日期</List.Item>
        </Picker>
      </div>
    )
  }

}

export default DateMonthWeek
