import React, { Component } from 'react'
import { List, DatePicker } from 'antd-mobile'

class DateMonthWeek extends Component {

  constructor (props) {
    super(props)
    this.state = {}
  }

  render () {
    return (
      <div>
        <DatePicker
          mode='date'
          title="选择日期"
          extra="开始日期"
          value={this.props.beginDate}
          format='YYYY-MM-DD'
          onChange={value => this.props.onTimeChange(value, 'beginDate')}
        >
          <List.Item arrow="horizontal">开始日期</List.Item>
        </DatePicker>
        <DatePicker
          mode='date'
          title="选择日期"
          extra="结束日期"
          value={this.props.endDate}
          format='YYYY-MM-DD'
          onChange={value => this.props.onTimeChange(value, 'endDate')}
        >
          <List.Item arrow="horizontal">结束日期</List.Item>
        </DatePicker>
      </div>
    )
  }

}

export default DateMonthWeek
