class Detail extends Component {

  constructor (props) {
    super(props)
    this.state = {
      title: '报表-测试数据',
      clientWidth: document.documentElement.clientWidth,
      clientHeight: document.documentElement.clientHeight,
      visible: false, // model
      headers: [
        {
          title: '主题',
          dataIndex: 'topicName',
          align: 'center',
          width: 100,
          render: (value, row, index) => {
            const obj = {
              children: value,
              props: {},
            }
            if (row.mergeTheme) {
              obj.props.rowSpan = row.mergeTheme
            } else {
              obj.props.rowSpan = 0
            }
            return obj
          },
        },
        {
          title: '指标',
          dataIndex: 'targetName',
          align: 'center',
          width: 100,
          render: (value, row, index) => {
            const obj = {
              children: <span style={{color: mainColor}} onClick={this.showModal.bind(this, row)}>{value}</span>,
              props: {
                style: {
                  color: row.hasAuth ? '#000' : '#ccc'
                }
              },
            }
            if (row.mergeTarget) {
              obj.props.rowSpan = row.mergeTarget
            } else {
              obj.props.rowSpan = 0
            }
            return obj
          },
        },
        {
          title: '维度值',
          dataIndex: 'dimValue',
          align: 'center',
          width: 100,
          render: (text, record) => <span style={{color: record.hasAuth ? '#000' : '#ccc'}}>{text}</span>,
        },
      ], // 主题在前，前三列
      targetComment: '', // 指标口径
      targetTitle: '', // 指标名称
      open: false, // 抽屉
      dict: {
        initTargetNameValue: ['指标1', '指标2指标2指标2指标2指标2指标2指标2', '指标3'], // 点击蒙层，设置不生效
        targetNameValue: ['指标1', '指标2指标2指标2指标2指标2指标2指标2', '指标3'], // 选中的指标
        targetNameFilter: [
          '指标1', '指标2指标2指标2指标2指标2指标2指标2', '指标3', '指标4', '指标5'
        ],  // 指标列表
        initDimValue: ['维度3', '维度4'], // 点击蒙层，设置不生效
        dimValue: ['维度3', '维度4'], // 选中的维度
        dimValueFilter: [
          '维度1维度1维度1维度1维度1维度1', '维度2', '维度3', '维度4'
        ], // 维度列表
        activeKey: ['0', '1'], // 过滤条件展开项
      },
      timeFilter: false, // 时间过滤，modal显示隐藏
      configBeginDate: new Date('2018-10-10'),
      configEndDate: new Date('2018-11-10'),
      initBeginDate: new Date('2018-10-10'),
      initEndDate: new Date('2018-11-10'),
      beginDate: new Date('2018-10-10'), // 时间过滤-开始时间 TODO [2018,11]
      endDate: new Date('2018-11-10'), // 时间过滤-结束时间 TODO [2018,15]
      infoType: 'd', // 日报、周报、月报 d 日报 w 周报 m 月报
    }
    this.renderResize = this.renderResize.bind(this)
    this.timeStampFormatToStr = this.timeStampFormatToStr.bind(this)
  }

  render () {
    const {beginDate,endDate,configBeginDate,configEndDate} = this.state
    let beginStr = this.timeStampFormatToStr(beginDate)
    let endStr = this.timeStampFormatToStr(endDate)
    let configBeginStr = this.timeStampFormatToStr(configBeginDate)
    let configEndStr = this.timeStampFormatToStr(configEndDate)
    const {targetNameFilter, dimValueFilter, activeKey, targetNameValue, dimValue} = this.state.dict
    const sidebar = (<div style={{height: '100%'}}>
      <div style={{height: '30px'}}/>
      <Accordion
        activeKey={activeKey}
        style={{height: this.state.clientHeight - 90}}
        className="my-accordion" onChange={this.onAccordionChange.bind(this)}>
        {/*指标筛选*/}
        <Accordion.Panel header="指标筛选" key="0">
          <List className="my-list">
            {targetNameFilter.map((ele, index) => {
              return (
                <CheckboxItem key={index} checked={~targetNameValue.indexOf(ele)}
                              onChange={this.onValueChange.bind(this, 'targetNameValue', ele)}>
                  {ele}
                </CheckboxItem>
              )
            })}
          </List>
        </Accordion.Panel>
        {/*维度筛选*/}
        <Accordion.Panel header="维度筛选" className="pad" key="1">
          <List className="my-list">
            {dimValueFilter.map((ele, index) => {
              return (<CheckboxItem key={index} checked={~dimValue.indexOf(ele)}
                                    onChange={this.onValueChange.bind(this, 'dimValue', ele)}>
                {ele}
              </CheckboxItem>)
            })}
          </List>
        </Accordion.Panel>
      </Accordion>
      <WingBlank>
        <div style={{textAlign: 'center', margin: '10px auto'}}>
          <Button onClick={this.onClearFilter.bind(this)} inline size="small">清空</Button>
          <Button inline size="small" type="primary"
                  onClick={this.onOpenChange.bind(this)}
          >确认</Button>
        </div>
      </WingBlank>
    </div>)
    return (
      <div className="Detail">
        <NavBar
          mode="light"
          rightContent={[
            <span key="0" onClick={this.onTimeFilter.bind(this)}
                  className="icon iconfont icon-shijian" style={{
              margin: '0 10px',
              color: (beginStr !== configBeginStr || endStr !== configEndStr) ? mainColor : 'rgb(101, 101, 101)',
            }}/>,
            <span onClick={this.onOpenChange.bind(this)} key="1" style={{
              color: (targetNameValue.length || dimValue.length) ? mainColor : 'rgb(101, 101, 101)',
            }} className="icon iconfont icon-Fill"/>,
          ]}
        >{this.state.title}</NavBar>
        {/*表格*/}
        <Table
          pagination={false}
          columns={this.state.headers}
          dataSource={data}
          bordered
          scroll={{x: 600, y: this.state.clientHeight - 110}}
        />
        {/*指标口径*/}
        <Modal
          title={this.state.targetTitle}
          transparent
          maskClosable={false}
          visible={this.state.visible}
          onClose={this.onClose.bind(this)}
          footer={[{text: '确定', onPress: () => this.onClose()}]}
        >
          {this.state.targetComment}
        </Modal>
        {/*抽屉*/}
        <Drawer
          className="my-drawer"
          position="right"
          style={{
            minHeight: document.documentElement.clientHeight - 100,
            height: document.documentElement.clientHeight,
            display: this.state.open ? '' : 'none',
            position: this.state.open ? 'absolute' : 'relative',
          }}
          enableDragHandle
          contentStyle={{color: '#A6A6A6', textAlign: 'center', paddingTop: 42}}
          sidebar={sidebar}
          children={0}
          open={this.state.open}
          onOpenChange={this.onOpenChange.bind(this)}
        >
        </Drawer>
        {/*时间筛选*/}
        <Modal
          title='选择日期范围'
          transparent
          maskClosable={false}
          closable={true}
          visible={this.state.timeFilter}
          onClose={this.onTimeCancel.bind(this)}
          footer={[
            {text: '取消', onPress: () => this.onTimeCancel()},
            {text: '确定', onPress: this.onTimeSearch.bind(this)}
          ]}>
          {this.state.infoType==='d' ? <DateDay
            beginDate={this.state.beginDate}
            endDate={this.state.endDate}
            onTimeChange={(value, type) => {
              this.onTimeChange(type, value)
            }}
          /> : <DateMonthWeek
            type={this.state.infoType==='w' ? 'w' : 'm'}
            beginDate={this.state.beginDate}
            endDate={this.state.endDate}
            onTimeChange={(value, type) => {
              this.onTimeChange(type, value)
            }}
          />}
        </Modal>
      </div>
    )
  }

  // 手风琴展开项
  onAccordionChange = (val) => {
    this.setState(prevState => {
      prevState.dict.activeKey = val
      return prevState
    })
  }

  // 筛选条件
  onValueChange = (key, ele, e) => {
    let targetNameValue = [...this.state.dict.targetNameValue]
    let dimValue = [...this.state.dict.dimValue]
    if (key === 'targetNameValue') {
      e.target.checked ? targetNameValue.push(ele) : targetNameValue.splice(targetNameValue.indexOf(ele), 1)
      this.setState(prevState => {
        prevState.dict.targetNameValue = targetNameValue
        return prevState
      })
    } else {
      e.target.checked ? dimValue.push(ele) : dimValue.splice(dimValue.indexOf(ele), 1)
      this.setState(prevState => {
        prevState.dict.dimValue = dimValue
        return prevState
      })
    }
  }

  // 清空搜索条件
  onClearFilter () {
    this.setState(prevState => {
      prevState.dict.targetNameValue = []
      prevState.dict.dimValue = []
      return prevState
    })
  }

  // 抽屉-起合状态
  onOpenChange = (...args) => {
    // 点击蒙层，本次设置不保留，不生效
    if (args.length === 2) {
      this.setState(prevState => {
        prevState.dict.targetNameValue = [...this.state.dict.initTargetNameValue]
        prevState.dict.dimValue = [...this.state.dict.initDimValue]
        return prevState
      })
    }
    // 点击确定
    if (args.length === 1) {
      this.setState(prevState => {
        prevState.dict.initTargetNameValue = [...this.state.dict.targetNameValue]
        prevState.dict.initDimValue = [...this.state.dict.dimValue]
        return prevState
      })
    }
    this.setState({open: !this.state.open})
  }

  // 屏幕旋转时，重新设置视口宽高
  renderResize = () => {
    let width = document.documentElement.clientWidth
    let height = document.documentElement.clientHeight
    this.setState({
      clientWidth: width,
      clientHeight: height
    })
  }

  componentDidMount () {
    // 屏幕旋转时，重新设置视口宽高
    this.renderResize()
  }

  componentWillMount () {
    window.addEventListener('resize', this.renderResize, false)
  }

  componentWillUnmount = () => {
    window.removeEventListener('resize', this.renderResize, false)
  }

  // 指标toast提示
  showModal = (row, e) => {
    // 现象：如果弹出的弹框上的 x 按钮的位置、和手指点击 button 时所在的位置「重叠」起来，
    // 会触发 x 按钮的点击事件而导致关闭弹框 (注：弹框上的取消/确定等按钮遇到同样情况也会如此)
    e.preventDefault() // 修复 Android 上点击穿透
    this.setState({
      targetComment: row.targetComment,
      targetTitle: row.targetName,
      visible: true,
    })
  }

  // 关闭指标口径toast提示
  onClose = () => {
    this.setState({
      visible: false,
    })
  }

  // 时间过滤
  onTimeFilter () {
    this.setState({timeFilter: true})
  }

  // 时间变化
  onTimeChange (key, date) {
    this.setState({[key]: date})
  }

  // 关闭时间过滤
  onTimeClose = () => {
    this.setState({timeFilter: false})
  }

  // 根据时间搜索
  // loginId: sunlz
  // infoId: 82
  // dimFront: false
  onTimeSearch () {
    let beginDate = this.timeStampFormatToStr(this.state.beginDate)
    let endDate = this.timeStampFormatToStr(this.state.endDate)
    let defaultTargetValue = [...this.state.dict.targetNameValue].join(',')
    let defaultDimValue = [...this.state.dict.dimValue].join(',')
    let data = `beginDate=${beginDate}&endDate=${endDate}&defaultTargetValue=${defaultTargetValue}&defaultDimValue=${defaultDimValue}&isSearch=${true}`
    console.log(data)
    this.onTimeClose()
    this.setState({
      initBeginDate: this.state.beginDate,
      initEndDate: this.state.endDate,
    })
  }

  // 格式化时间
  timeStampFormatToStr (ts) {
    if (!ts)
      return ''
    if (typeof ts === 'string')
      return ts
    if (ts instanceof Array)
      return ts.join('')
    const d = new Date(ts)
    const ydm = [d.getFullYear(), d.getMonth() + 1, d.getDate()].map(el => String(el).padStart(2, 0)).join('-')
    return ydm
  }

  // 取消，条件不生效
  onTimeCancel () {
    this.onTimeClose()
    this.setState({
      beginDate: this.state.initBeginDate,
      endDate: this.state.initEndDate,
    })
  }


}

export default Detail
